import { TimePeriod, TimeOffsets } from './constants';

const getLabels = () => {
  const curDate = new Date();
  const now = curDate.getTime();
  const today = curDate.setHours(0, 0, 0, 0);

  return [
    {
      label: TimePeriod.TOMORROW,
      startDate: today + TimeOffsets.DAY_OFFSET,
      endDate: today + (TimeOffsets.DAY_OFFSET * 2),
    }, {
      label: TimePeriod.NOW,
      startDate: now - TimeOffsets.NOW_OFFSET,
      endDate: now,
    }, {
      label: TimePeriod.TODAY,
      startDate: today,
      endDate: today + TimeOffsets.DAY_OFFSET,
    }, {
      label: TimePeriod.YESTERDAY,
      startDate: today - TimeOffsets.DAY_OFFSET,
      endDate: today,
    },
  ];
};

/**
 * Convert valid date to date label
 *
 * @name ui-bb-date-label-filter-ng.dateLabelFilter
 * @type {function}
 * @param date {string|number|Date} Date string or timestamp
 * @returns {string} Date label or date that was passed initially
 */
const dateLabelFilter = (date) => {
  const parsed = Number.isInteger(date) ? date : Date.parse(date);
  if (isNaN(parsed)) {
    return date;
  }

  const labels = getLabels();
  const dateLabel = labels.find(
    label => parsed < label.endDate && parsed >= label.startDate
  );

  return dateLabel ? dateLabel.label : date;
};

export default dateLabelFilter;



