/* eslint import/prefer-default-export: "off" */
/**
 * @description
 * date labels enum
 *
 * @name TimePeriod
 * @type {object}
 */
export const TimePeriod = {
  NOW: 'now',
  TODAY: 'today',
  YESTERDAY: 'yesterday',
  TOMORROW: 'tomorrow',
};

/**
 * @description
 * Milliseconds offsets
 *
 * @name TimeOffsets
 * @type {object}
 */

export const TimeOffsets = {
  NOW_OFFSET: 1000 * 60 * 15, // 15 minutes
  DAY_OFFSET: 24 * 60 * 60 * 1000, // 24 hours
};



